import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CanDeactivateGuard } from 'src/app/shared/guards/can-deactivate.guard';
import { AccountClosureDetailsComponent } from './account-closure-details/account-closure-details.component';
import { InitiateAccountClosureComponent } from './initiate-account-closure/initiate-account-closure.component';
import { ViewReportAccClosureComponent } from './view-report-acc-closure/view-report-acc-closure.component';

const routes: Routes = [
  {
    path:"",
    component:InitiateAccountClosureComponent
  },
  {
    path:"create",
    component:AccountClosureDetailsComponent,
    canDeactivate: [CanDeactivateGuard]
  },
  {
    path:"viewReport",
    component:ViewReportAccClosureComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class InitiateAccountClosureRoutingModule { }
